import React from 'react';

import './Header.css';

const Header = () => (
  <header className="header">
    <h1>React Trend</h1>
    <p>Simple, elegant trend graphs for React.js.</p>
    <p>A project by <a href="https://unsplash.com">Unsplash</a>.</p>
  </header>
);

export default Header;



